.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 150px;
}

.top-bar {
  display: flex;
  justify-items: flex-start;
  width: 650px;

  background-color: rgb(48, 44, 44);

  padding: 20px;
  border-radius: 20px;

  border: 2px solid black;
  margin-bottom: 15px;

  box-shadow: 5px 5px 5px 0px rgba(0,0,0,.6);
}

.formBox {
  width: 100%;
}

.top-bar input[type=button] {
  width: 150px;
  padding: 10px;
  padding-right: 5px;
  margin-left: 20px;
  border-radius: 5px;

  background-color: rgb(90, 86, 86);

  color: white;
  font-weight: 700;
  font-family: consolas;
  font-size: 1.1rem;
  letter-spacing: 0.4rem;
  text-align: center;
  
}

.top-bar input[type=button]:hover {
  background-color: rgb(49, 48, 48);

  cursor: pointer;
}

.top-bar input[type="text"] {
  border-radius: 5px;
  padding: 5px;
  text-align: left;
  padding-left: 15px;

  font-family: consolas;
  font-size: 1.1rem;
  width: 70%;
}

.todo-body {
  display: flex;

  border-radius: 15px;
  background-color: rgb(48, 44, 44);
  
  width: 500px;
  height: 500px;

  padding: 15px;
  box-shadow: 5px 5px 5px 0px rgba(68, 64, 64, 0.6);
}

.left-nav {
  width: 40%;
  background-color: #529fc2b7;
  margin-right: 25px;
  border-radius: 15px;
  padding: 15px;

  color: white;
  font-size: 15px;
}

.todos { 
  background-color: #5277c277;
  width: 60%;
  border-radius: 15px;
  padding: 15px;

  color:white;
}

.project-item {
  background-color: rgba(64, 65, 78, 0.849);
  padding: 5px;
  border-radius: 5px;
  margin: 15px 0px;
}

.todo-item {
  background-color: rgba(216, 189, 102, 0.849);
  padding: 5px;
  border-radius: 5px;
  margin: 5px;
}

#help-note {
  color: rgb(0, 0, 0);
  font-size: 20px;
  margin-top: 15px;
  font-size: 15px;
}